
* {box-sizing: border-box;}

html, body {
  height: 100%;
}

html {
  font-family: 'Lucida Grande', Helvetica, Arial, sans-serif;
  background-color: #123;
}

body {
  margin: 0;
}

.flex-separator {
  flex: 1;
}

.home-header {
  display: block;
  text-decoration: none;
  text-align: center;
  background-color: #fff;
  position: relative;

  .logo {
    padding: 30px 20px 0px;
    font-size: 0;

    img {width: 140px;}
  }

  .name {
    margin: 0px auto;
    padding: 20px;
    max-width: 640px;

    h1, h2, p {
      margin: 0;
      padding: 0;
    }

    h1 {
      color: #234;
      font-size: 1.8em;

      a {
        text-decoration: none;
        color: inherit;
      }
    }

    h2 {
      font-size: 1em;
      font-weight: normal;
      color: #678;
      padding-bottom: 2em;
    }

    p {
      color: #456;
      font-size: 1.2em;
      padding-bottom: 1em;
    }
  }

  .links {
    a {
      display: inline-block;
      padding: 15px;
      text-align: center;
      text-decoration: none;
      flex: 1;
      color: #555;
      font-size: 1em;

      &:hover {
        color: #08f;
      }

      &:first-child {
        border-left: none;
      }

      &:last-child {
        border-right: none;
      }
    }
  }
}

section {
  padding: 20px;
  h2 {
    text-align: center;
    color: #89a;
  }
}

.projects {
  display: flex;
  align-items: stretch;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-grow: 3;
  text-align: center;
  border-radius: 2px;
}

.project {
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 10px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 0 5px rgba(0,0,0,0.2);
  min-width: 240px;

  .about {
    flex: 1;
    justify-content: center;
    align-items: center;
    padding: 20px;

    h3, p {
      margin: 0px;
    }
  }

  .links {
    display: flex;
    justify-content: center;
    align-items: stretch;
    border-top: 1px solid #eee;

    a {
      display: block;
      flex: 1;
      padding: 15px;
      text-decoration: none;
      color: inherit;

      border-right: 1px solid #eee;

      &:last-child {
        border-right: none;
      }
    }

    a:hover {
      color: #08f;
    }
  }
}

.document {
  background-color: #fff;
  padding: 0px 20px 40px;

  .content {
    max-width: 620px;
    margin: 0px auto;
    text-align: center;

    img {
      width: 100%;
      margin-bottom: 2em;
    }

    h1 {
      margin: 0px;
    }

    p {
      color: #345;
      font-size: 1.1em;
      margin: 0px;
      padding-bottom: 1.5em;
    }

    a {color: #08f;}
  }
}
